import CouponGoods from '../../components/lists/CouponGoods';
import ThemeOne from '../../components/recommend/ThemeOne';
import indexMock from '../../mock/index.js';
import redirect from '../../mixins/redirect.js'
import global from '../../mixins/global.js'
import brands from './components/brands'
import hotClass from './components/hotClass'
import hotGoods from './components/hotGoods'
import goodsSwiper from './components/goodsSwiper'
import goods from '../../mock/goods'

export default {
  mixins: [redirect,global],
  components: {
    CouponGoods,
    ThemeOne,
    brands,
    hotClass,
    hotGoods,
    goodsSwiper,
  },
  data() {
    return {
      titleNViewBackground: '',
      swiperCurrent: 0,
      swiperLength: 0,
      carouselList: [],
      goodsList: goods.goodsLists,
      category: indexMock.category,
      recommend: indexMock.recommend,
      detail:{
         carousel:[],
          brands_item:[],
          hot_goods:[],
          hotClass :[],
          goods_swiper:[],
      },
      loading:true
    };
  },
 mounted(){
 },
  onLoad() {
    this.userLogin();
    this.getDetail();
  },
  onShow(){
    uni.showTabBar()
  },
  methods: {
    //轮播图切换修改背景色
    swiperChange(e) {
      const index = e.detail.current;
      this.swiperCurrent = index;
      // this.titleNViewBackground = this.carouselList[index].background;
    },
    swiperClick(item){
      if(item.value){
        if(item.show_type==='goods'){
          this.toGoods(item.value)
        }else if(item.show_type==='class'){
          this.toQueryLink('/pages/goods/lists',{
            gc_id:item.value
          })
        }else{
          if(item.selectShopType==='brand'){
            this.toQueryLink('/pages/brand/lists',{
              shopid:item.value
            })
          }else{
            this.toQueryLink('/pages/shop/index',{
              shopid:item.value
            })
          }
        }
      }
    },
    search() {
      uni.navigateTo({
        url: '/pages/goods/search',
      });
    },
    toGoodsLists() {
      uni.navigateTo({
        url: '/pages/goods/lists',
      });
    },
    getDetail(){
        // uni.showLoading({
        // });
        this.$http({
            aurl:'getIndex',
            success:res => {
                const detailItem = res.data.result
                this.detail.carousel = detailItem.carousel
                this.detail.brands_item = detailItem.brands_item
                this.detail.hot_goods = detailItem.hot_goods
                this.detail.hotClass = detailItem.hotClass
                this.detail.goods_swiper = detailItem.goods_swiper
            },
            fail:error => {
                console.log(error)
                uni.hideLoading()
                this.loading = false
            }
        })
      },
  },
};
